<template>
  <div class="main-padding">
    <el-card>
      <el-form :inline="true">
        <el-form-item>
          <el-button @click="$router.push(`/picture/handle`)">处理</el-button>
        </el-form-item>
        <el-form-item>
          <el-button
            v-if="
              $enum.equal(
                $route.query.life,
                $enum.PictureLifeState,
                $enum.PictureLifeState.DISAPPEAR
              )
            "
            @click="del"
            type="danger"
            :disabled="!selectList.length"
            >物理删除</el-button
          >
        </el-form-item>
      </el-form>
    </el-card>
    <el-card style="margin-top: 20px;">
      <el-form :model="query" :inline="true">
        <el-form-item>
          <el-date-picker
            v-model="query.date"
            type="daterange"
            value-format="yyyy-MM-dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            clearable
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="query.name"
            placeholder="图片名"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-select v-model="query.privacy" placeholder="显示状态" clearable>
            <el-option
              v-for="(item, key) in $enum.PrivacyState"
              :key="key"
              :label="item"
              :value="key"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select v-model="query.life" placeholder="生命状态" clearable>
            <el-option
              v-for="(item, key) in $enum.PictureLifeState"
              :key="key"
              :label="item"
              :value="key"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select v-model="query.master" placeholder="是否主图片" clearable>
            <el-option label="是" :value="true"> </el-option>
            <el-option label="否" :value="false"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select
            v-model="query.userId"
            filterable
            remote
            clearable
            reserve-keyword
            placeholder="用户名搜索ID"
            :remote-method="remoteMethod"
            :loading="optionsLoading"
            @change="changeUserId"
          >
            <el-option
              v-for="item in userList"
              :key="item.phone"
              :label="item.name + `(${item.phone})`"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="query.phone"
            placeholder="用户手机"
            clearable
            @input="query.userId = ''"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="changePage(1)">查询</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card style="margin-top: 20px;">
      <el-table
        border
        style="width: 100%"
        :data="list"
        @selection-change="
          (val) => {
            this.selectList = val
          }
        "
      >
        <el-table-column
          type="selection"
          width="40"
          style="text-align: center"
          v-if="
            $enum.equal(
              $route.query.life,
              $enum.PictureLifeState,
              $enum.PictureLifeState.DISAPPEAR
            )
          "
        >
        </el-table-column>
        <el-table-column label="图片" width="262">
          <template slot-scope="scope">
            <a
              :href="`http://localhost:3000/picture/` + scope.row.id"
              target="_blank"
            >
              <img v-lazy="$img.secdra(scope.row.url, 'specifiedWidth')" />
            </a>
          </template>
        </el-table-column>
        <el-table-column label="图片名" prop="name"></el-table-column>
        <el-table-column label="显示状态" width="100">
          <template slot-scope="scope">
            {{ $enum.PrivacyState[scope.row.privacy] }}
          </template>
        </el-table-column>
        <el-table-column label="生命状态" width="100">
          <template slot-scope="scope">
            {{ $enum.PictureLifeState[scope.row.life] }}
          </template>
        </el-table-column>
        <el-table-column label="创建时间" prop="createDate"></el-table-column>
        <el-table-column label="创建用户" width="100">
          <template slot-scope="scope">
            <a
              :href="`http://localhost:3000/user/` + scope.row.user.id"
              target="_blank"
            >
              {{ scope.row.user.name }}
            </a>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="230">
          <template slot-scope="scope">
            <el-button @click="listBySuit(scope.$index)" size="mini">
              查看套图
            </el-button>
            <template
              v-if="
                $enum.equal(
                  scope.row.life,
                  $enum.PictureLifeState,
                  $enum.PictureLifeState.EXIST
                )
              "
            >
              <el-button
                @click="updatePrivacy(scope.row.id, scope.row.privacy)"
                type="primary"
                size="mini"
              >
                {{ opposite(scope.row.privacy) }}
              </el-button>
              <el-button
                @click="remove(scope.row.id, scope.row.life)"
                type="warning"
                size="mini"
              >
                逻辑删除
              </el-button>
            </template>
            <template
              v-if="
                $enum.equal(
                  scope.row.life,
                  $enum.PictureLifeState,
                  $enum.PictureLifeState.DISAPPEAR
                )
              "
            >
              <el-button
                @click="reduction(scope.row.id, scope.row.life)"
                type="success"
                size="mini"
              >
                还原
              </el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        ref="pagination"
        layout="prev, pager, next, jumper"
        background
        :page-size="pageable.size"
        :total="totalElements"
        :current-page="pageable.page"
        @current-change="changePage"
      >
      </el-pagination>
    </el-card>
    <el-drawer :visible.sync="drawerVisible" :with-header="false" size="90%">
      <el-table border style="width: 100%" :data="suitList" height="100vh">
        <el-table-column label="图片" width="262">
          <template slot-scope="scope">
            <a
              :href="`http://localhost:3000/picture/` + scope.row.id"
              target="_blank"
            >
              <img v-lazy="$img.secdra(scope.row.url, 'specifiedWidth')" />
            </a>
          </template>
        </el-table-column>
        <el-table-column label="图片名" prop="name"></el-table-column>
        <el-table-column label="显示状态" width="100">
          <template slot-scope="scope">
            {{ $enum.PrivacyState[scope.row.privacy] }}
          </template>
        </el-table-column>
        <el-table-column label="生命状态" width="100">
          <template slot-scope="scope">
            {{ $enum.PictureLifeState[scope.row.life] }}
          </template>
        </el-table-column>
        <el-table-column label="创建时间" prop="createDate"></el-table-column>
        <el-table-column label="创建用户" width="100">
          <template slot-scope="scope">
            <a
              :href="`http://localhost:3000/user/` + scope.row.user.id"
              target="_blank"
            >
              {{ scope.row.user.name }}
            </a>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="270">
          <template slot-scope="scope">
            <template
              v-if="
                $enum.equal(
                  scope.row.life,
                  $enum.PictureLifeState,
                  $enum.PictureLifeState.EXIST
                )
              "
            >
              <el-button
                @click="updatePrivacy(scope.row.id, scope.row.privacy)"
                type="primary"
                size="mini"
              >
                {{ opposite(scope.row.privacy) }}
              </el-button>
              <el-button
                @click="remove(scope.row.id, scope.row.life)"
                type="warning"
                size="mini"
              >
                逻辑删除
              </el-button>
            </template>
            <template
              v-if="
                $enum.equal(
                  scope.row.life,
                  $enum.PictureLifeState,
                  $enum.PictureLifeState.DISAPPEAR
                )
              "
            >
              <el-button
                @click="reduction(scope.row.id, scope.row.life)"
                type="success"
                size="mini"
              >
                还原
              </el-button>
              <el-button type="danger" size="mini">
                物理删除
              </el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
    </el-drawer>
  </div>
</template>

<script>
import { QueryForm } from "./script/model"
import { Pageable } from "../../assets/script/model"
import { mapActions } from "vuex"
import qs from "qs"

export default {
  data() {
    return {
      pageable: new Pageable(),
      query: null,
      list: [],
      totalElements: 0,
      userList: [],
      optionsLoading: false,
      drawerVisible: false,
      suitList: [],
      selectList: []
    }
  },
  watch: {
    $route: {
      handler(to) {
        this.pageable.page = parseInt(to.params.page)
        this.pageable.sort = "createDate,desc"
        this.query = new QueryForm(to.query)
        this.paging()
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions("user", { AListUser: "AList" }),
    ...mapActions("picture", [
      "APaging",
      "AListBySuit",
      "AUpdatePrivacy",
      "ABatchRemove",
      "ABatchDelete",
      "AReduction"
    ]),
    changePage(page) {
      let query = Object.assign({}, this.query)
      query.date = query.date ? query.date.join(",") : []
      this.$router.push(`/picture/${page}?${qs.stringify(query)}`)
    },
    async paging() {
      let loading = this.$loading()
      let paging = (
        await this.APaging(Object.assign({}, this.pageable, this.query))
      ).data
      loading.close()
      this.list = paging.content || []
      this.totalElements = paging.totalElements || 0
      document.getElementsByTagName("main")[0].scrollTo(0, 0)
    },
    async listBySuit(index) {
      let row = this.list[index]
      try {
        var pixivId = row.url.split("_")[0]
        let loading = this.$loading()
        this.suitList = (await this.AListBySuit({ pixivId })).data
        loading.close()
        this.drawerVisible = true
      } catch (e) {
        this.$notify({ message: "该图片不是pixiv采集图片" })
      }
    },
    async updatePrivacy(id, privacy) {
      const result = await this.AUpdatePrivacy({ id })
      if (result.status === 200) {
        this.$notify({ message: "成功" })
        this.changeState(
          id,
          "privacy",
          this.$enum.getKey(this.$enum.PrivacyState, this.opposite(privacy))
        )
      } else {
        this.$notify({ message: "失败" })
      }
    },
    async remove(id, life) {
      const result = await this.ABatchRemove({ idList: [id] })
      if (result.status === 200) {
        this.$notify({ message: "成功" })
        this.changeState(
          id,
          "life",
          this.$enum.getKey(
            this.$enum.PictureLifeState,
            this.oppositeLife(life)
          )
        )
      } else {
        this.$notify({ message: "失败" })
      }
    },
    async reduction(id, life) {
      const result = await this.AReduction({ idList: id })
      if (result.status === 200) {
        this.$notify({ message: "成功" })
        this.changeState(
          id,
          "life",
          this.$enum.getKey(
            this.$enum.PictureLifeState,
            this.oppositeLife(life)
          )
        )
      } else {
        this.$notify({ message: "失败" })
      }
    },
    del() {
      this.$confirm("物理删除，敏感操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          const result = await this.ABatchDelete({
            idList: this.selectList.map((item) => item.id)
          })
          if (result.status === 200) {
            this.$notify({ message: "成功" })
            this.changePage(1)
          } else {
            this.$notify({ message: "失败" })
          }
        })
        .catch(() => {})
    },
    changeState(id, key, value) {
      let row = this.list.find((item) => item.id === id)
      if (row) {
        row[key] = value
      }
      let suitRow = this.suitList.find((item) => item.id === id)
      if (suitRow) {
        suitRow[key] = value
      }
    },
    opposite(privacy) {
      if (
        this.$enum.equal(
          privacy,
          this.$enum.PrivacyState,
          this.$enum.PrivacyState.PRIVATE
        )
      ) {
        return this.$enum.PrivacyState.PUBLIC
      } else {
        return this.$enum.PrivacyState.PRIVATE
      }
    },
    oppositeLife(life) {
      if (
        this.$enum.equal(
          life,
          this.$enum.PictureLifeState,
          this.$enum.PictureLifeState.EXIST
        )
      ) {
        return this.$enum.PictureLifeState.DISAPPEAR
      } else {
        return this.$enum.PictureLifeState.EXIST
      }
    },
    async remoteMethod(name) {
      if (name !== "") {
        this.optionsLoading = true
        this.userList = (await this.AListUser({ name })).data
        this.optionsLoading = false
      } else {
        this.userList = []
      }
    },
    changeUserId(id) {
      this.query.phone = this.userList.find((item) => (item.id = id)).phone
    }
  }
}
</script>

<style type="text/less" lang="less" scoped></style>
